<template>
  <div id="app">
    <b-container :fluid="true" v-if="loadFinish">
      <b-row>
        <b-col cols="1">
          <b-input-group>
            <b-form-input placeholder="Search" @keyup.enter="search = search2" v-model="search2"></b-form-input>
            <b-input-group-text v-if="search !== ''" @click="search = '', search2 = ''">
              <b-icon icon="x"></b-icon>
            </b-input-group-text>
          </b-input-group>
          <ul :style="'height: ' + height + 'px'">
            <li v-for="(value) in dataset" :key="value.key" v-show="value.label.indexOf(search) > -1">
              <a :class="value.group === watchGroup ? 'active' : ''"  @click="watchGroup = value.group">
                {{value.label}}
              </a>
            </li>
          </ul>
        </b-col>
        <b-col cols="11">
          <b-container ref="b_height" :fluid="true">
            <b-row>
              <b-col v-for="(value) in fn_grid1" :key="value.key">
                <div>
                  {{value.label}}  <b-button @click="$bvToast.show('toast' + value.key)">url</b-button>   <b-button @click="openNewTab(value.addr)">새탭</b-button>
                  <b-toast :id="'toast' + value.key" :title="value.label" style="z-index: 2000;" no-auto-hide>
                    {{value.addr}}
                  </b-toast>
                  <br>
                  <b-embed :ref="'iframe' + value.key"
                    type="iframe"
                    aspect="16by9"
                    :src="value.addr"
                    style="margin-top: 5px"
                  ></b-embed>
                </div>
              </b-col>
              <b-col v-for="(value) in fn_grid2" :key="value.key">
                <div>
                  {{value.label}}  <b-button @click="$bvToast.show('toast' + value.key)">url</b-button>   <b-button @click="openNewTab(value.addr)">새탭</b-button>
                  <b-toast :id="'toast' + value.key" :title="value.label" style="z-index: 2000;" no-auto-hide>
                    {{value.addr}}
                  </b-toast>
                  <br>
                  <b-embed :ref="'iframe' + value.key"
                    type="iframe"
                    aspect="16by9"
                    :src="value.addr"
                    style="margin-top: 5px"
                  ></b-embed>
                </div>
              </b-col>
            </b-row>
            <br>
            <b-row> 
              <b-col v-for="(value) in fn_grid3" :key="value.key">
                <div>
                  {{value.label}}  <b-button @click="$bvToast.show('toast' + value.key)">url</b-button>   <b-button @click="openNewTab(value.addr)">새탭</b-button>
                  <b-toast :id="'toast' + value.key" :title="value.label" style="z-index: 2000;" no-auto-hide>
                    {{value.addr}}
                  </b-toast>
                  <br>
                  <b-embed :ref="'iframe' + value.key"
                    type="iframe"
                    aspect="16by9"
                    :src="value.addr"
                    style="margin-top: 5px"
                  ></b-embed>
                </div>
              </b-col>
              <b-col v-for="(value) in fn_grid4" :key="value.key">
                <div>
                  {{value.label}}  <b-button @click="$bvToast.show('toast' + value.key)">url</b-button>   <b-button @click="openNewTab(value.addr)">새탭</b-button>
                  <b-toast :id="'toast' + value.key" :title="value.label" style="z-index: 2000;" no-auto-hide>
                    {{value.addr}}
                  </b-toast>
                  <br>
                  <b-embed :ref="'iframe' + value.key"
                    type="iframe"
                    aspect="16by9"
                    :src="value.addr"
                    style="margin-top: 5px"
                  ></b-embed>
                </div>
              </b-col>
            </b-row>
          </b-container>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>
<script>

export default {
  created () {
  },
  data () {
    return {
      loadFinish: false,
      watchGroup: 0,
      search: '',
      search2: '',
      height: 0
    }
  },
  mounted () {
    this.$http.get('/api/dataset')
    .then((response) => {
      this.dataset = response.data
      let group = 0
      let key = 0
      for (let index = 0; index < this.dataset.length; index++) {
        if (this.dataset.addr === '' || this.dataset.label === '') { continue }
        this.dataset[index].key = key
        key++
        this.dataset[index].group = group
        if (key % 4 === 0) {
          group++
        }
      }
      this.height = window.innerHeight
      window.addEventListener('resize', this.resizeApp)
      this.loadFinish = true
    })
  },
  methods: {
    iframUrl(key) {
      console.log(this.$refs['iframe' + key])
    },
    resizeApp () {
      this.height = this.$refs.b_height.clientHeight
    },
    openNewTab(value) {
      window.open(value)
    }
  },
  computed: {
    fn_grid1 () {
      return this.dataset.filter(item => item.key % 4 === 0 && this.watchGroup === item.group)
    },
    fn_grid2 () {
      return this.dataset.filter(item => item.key % 4 === 1 && this.watchGroup === item.group)
    },
    fn_grid3 () {
      return this.dataset.filter(item => item.key % 4 === 2 && this.watchGroup === item.group)
    },
    fn_grid4 () {
      return this.dataset.filter(item => item.key % 4 === 3 && this.watchGroup === item.group)
    }
  }
}
</script>

